import { ErrorCodesMapRequiredApplicationError } from '@wix/fe-essentials/error-handler';
import { Translate } from '../../services';
import { GetExportAsyncJobApplicationErrors } from '@wix/bex-utils/@wix/ambassador-fedinfra-exportservice-v1-export-async-job/types';
import { commonExportAsyncJobErrors } from './commonExportAsyncJobErrors';

export const getExportAsyncJobErrors = ({
  translate: t,
  retryFn,
}: {
  translate: Translate;
  retryFn?: () => void;
}): ErrorCodesMapRequiredApplicationError<GetExportAsyncJobApplicationErrors> => {
  return {
    applicationError: {
      job_not_found: () => ({
        message: t('cairo.export.GetExportAsyncJob.job-not-found.error'),
        action: {
          text: t('cairo.error.tryAgain.cta'),
          onClick: retryFn,
        },
      }),
      get_async_job_internal: () => ({
        message: t('cairo.export.get-async-job.error.internal'),
        action: {
          text: t('cairo.error.tryAgain.cta'),
          onClick: retryFn,
        },
      }),
      method_endpoint_permission_denied: false,
    },
    statusCodeError: {
      ...commonExportAsyncJobErrors({ translate: t }),
    },
  };
};
